body {
  box-sizing: content-box;
  margin: 0;
  background-color: black;
  color: white;
  font-family: 'Helvetica', 'Helvetica Neue', 'Arial',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Courier New', monospace;
}

#header {
    display: flex;
    width: 100vw;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    position: fixed;
    z-index: 1;
    background-color: black;
}

#header-logo {
    width: 300px;
}

#navbar {
    display: flex;
    width: 50%;
    justify-content: space-evenly;
    align-items: center;
    font-size: 24px;
    transition: 200ms;
}

.navlink {
    padding-top: 15px;
    color: white;
    text-decoration: none;
    transition: 200ms;
    -webkit-user-select: none;
            user-select: none;
}
.navlink.active {
    border: 0px solid red;
    border-bottom-width: 2px;
    color: red;
}
.navlink:hover {
    color: red;
}

@media screen and (max-width: 900px) {
    #header-logo {
        width: 100px;
    }
    #navbar {
        width: 100%;
        font-size: 16px;
    }
}
.about-card {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    padding: 50px;
    padding-bottom: 0;
}
.about-card-image {
    display: flex;
    height: 80%;
    width: 50%;
}
.about-card-content {
    display: flex;
    flex-direction: column;
    height: 80%;
    width: 50%;
    padding: 0% 5%;
}
.about-card-heading {
    color: rgb(235, 0, 40);
    font-size: 70px;
    padding: 0% 5%;
    margin-bottom: 0;
    flex-wrap: wrap;
    height: -webkit-max-content;
    height: max-content;
    max-width: 400px;
}
.about-card-text {
    font-size: 24px;
    padding: 5%;
    text-align: left;
    transition: 200ms;
    background-color: white;
    color: black;
    border-radius: 40px;
}

@media screen and (max-width: 900px) {
    .about-card {
        max-width: 100vw;
        height: 60vh;
        padding: 0;
    }
    .about-card-image {
        display: none;
    }
    .about-card-content {
        display: flex;
        height: 80%;
        width: 80%;
        padding: 0% 0%;
    }
    .about-card-heading {
        font-size: 40px;
        padding: 0% 3%;
    }
    .about-card-text {
        font-size: 18px;
        padding: 5%;
        text-align: left;
    }
}
a {
    color: rgb(255, 70, 70);
}

#home {
    background-image: url(/static/media/x-neon-sign.ac7174b6.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: contain;
    height: 100vh;
    overflow-x: hidden;
}

#logo-container {
    display: flex;
    height: inherit;
    justify-content: flex-end;
    align-items: center;
}

#home-logo {
    max-width: 60vw;
    background-color: black;
}

#hero-container {
    display: flex;
    flex-direction: column;
    height: inherit;
    background-color: transparent;
    justify-content: center;
    align-items: center;
    font-size: 3vw;
    padding: 0% 10%;
    text-align: center;
    -webkit-user-select: none;
            user-select: none;
}

#hero-button {
    width: 250px;
    height: 75px;
    font-size: 24px;
    border-radius: 40px;
    color: white;
    border: 0;
    background-color: red;
    transition: 200ms;
    margin-bottom: 500px;
}
#hero-button:hover {
    background-color: white;
    color: red;
    box-shadow: 5px 5px red;
}
#hero-button:active {
    box-shadow: none;
}

#info-container {
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 300px;
    width: 100%;
    padding-bottom: 20px;
}
#info-heading {
    font-size: 2rem;
}
#footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    width: 100%;
    height: 400px;
}
.footer-heading {
    font-size: 20px;
    margin: 0;
}
.footer-section {
    display: flex;
    flex-direction: column;
    width: 250px;
}
.social-icon {
    max-width: 25px;
    margin-right: 20px;
    margin-top: 20px;
}
#license-line {
    text-align: center;
    padding-bottom: 10px;
}

@media screen and (max-width: 900px) {
    #home {
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
        overflow-x: hidden;
    }
    #home-logo {
        max-width: 70vw;
    }
    #hero-container {
        padding: 0% 5%;
        font-size: 5vw;
    }
    #logo-container {
        justify-content: flex-start;
    }
    #hero-button {
        width: 150px;
        height: 50px;
        font-size: 16px;
        border-radius: 30px;
    }
    #hero-button:hover {
        box-shadow: 3px 3px red;
    }
    .footer-section {
        width: 150px;
    }
    #footer {
        padding: 5%;
        background-color: black;
    }
    #license-line {
        background-color: black;
        font-size: 14px;
        width: 100%;
        padding: 5% 20%;
    }
}
#talks-container {
    display: flex;
    flex-direction: row;
}

#timeline-pane {
    margin-top: 100px;
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 50px;
    position: fixed;
    background-color: black;
    z-index: 1;
}
#date-pane {
    display: flex;
    height: 100%;
    width: 250px;
    margin-top: 100px;
    position: fixed;
    z-index: 1;
    margin-left: 50px;
}
#progress-level {
    margin-top: 5px;
    height: 10px;
    width: 15px;
    min-height: 50px;
    margin-left: 30px;
    border-radius: 10px;
    background-color: rgb(235, 0, 40);
}
#content-pane {
    display: flex;
    flex-direction: column;
    min-width: 50%;
    max-width: 1200px;
    height: -webkit-max-content;
    height: max-content;
    margin-top: 100px;
    margin-left: 300px;
}
#event-pane {
    height: 700px;
    padding: 0px 50px;
    border-radius: 50px;
    margin-bottom: 100px;
    border: 5px solid white;
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: darken;
}
#date {
    grid-gap: 10px;
    gap: 10px;
    color: rgb(235, 0, 40);
    display: flex;
    align-items: center;
    height: 50px;
    font-size: 30px;
    width: 240px;
    border-radius: 10px;
    margin-left: 10px;
}
#arrow {
    max-width: 50px;
    font-size: 30px;
    color: red;
}
.event-title {
    font-size: 40px;
    text-align: center;
    max-width: 600px;
    border: 0;
    padding-bottom: 10px;
    border-bottom-width: 5px;
    border-color: rgb(235, 0, 40);
    border-style: solid;
    width: -webkit-max-content;
    width: max-content;
}
.event-description {
    font-size: 24px;
}
.event-button {
    width: 150px;
    height: 50px;
    border-radius: 50px;
    border: 0;
    background-color: rgb(235, 0, 40);
    color: white;
    font-size: 20px;
}

@media screen and (max-width: 900px) {
    #timeline-pane {
        margin-top: 50px;
        height: 100%;
        width: 20px;
    }
    #date-pane {
        width: 100px;
        margin-top: 50px;
        margin-left: 10px;
    }
    #progress-level {
        margin-top: 5px;
        width: 5px;
        margin-left: 10px;
    }
    #content-pane {
        width: 100%;
        height: -webkit-max-content;
        height: max-content;
        margin-top: 50px;
        margin-left: 100px;
    }
    #event-pane {
        height: 700px;
        padding: 0px 20px;
        border-radius: 30px;
        margin-bottom: 100px;
    }
    #date {
        grid-gap: 5px;
        gap: 5px;
        height: 50px;
        font-size: 12px;
        width: 80px;
    }
    #arrow {
        max-width: 20px;
        font-size: 10px;
    }
    .event-title {
        font-size: 20px;
        max-width: 200px;
        border: 0;
        padding-bottom: 10px;
        border-bottom-width: 5px;
        width: -webkit-max-content;
        width: max-content;
    }
    .event-description {
        font-size: 14px;
        margin-bottom: 20px;
    }
    .event-button {
        width: 100px;
        height: 50px;
        border-radius: 30px;
        font-size: 14px;
    }
}
.social-icon {
    max-width: 25px;
}
.member-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 5px 5px rgb(254, 115, 115);
    height: 400px;
    width: 300px;
    background-color: black;
    border: 2px solid black;
    transition: 200ms;
}
.member-card-image {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
#social-links {
    display: flex;
    flex-direction: row;
}
.member-card-content {
    display: flex;
    align-items: flex-start;
    margin-top: 20px;
    grid-gap: 10px;
    gap: 10px;
    width: 90%;
    flex-direction: column;
}
.member-card-content > h1 {
    margin: 0;
}
.member-name {
    font-size: 24px;
}
.member-role {
    font-size: 20px;
}
.member-team {
    font-size: 16px;
}
#team-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    min-height: 90vh;
    padding-bottom: 100px;
    height: -webkit-max-content;
    height: max-content;
    padding-top: 100px;
    background-color: white;
}
#team-heading {
    text-align: center;
    color: rgb(235, 0, 40);
    margin-bottom: 50px;
    font-size: 3rem;
}
#members-container {
    display: flex;
    width: 90%;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 50px;
    gap: 50px;
}
